$tablet-width: 768px;
$desktop-width: 1024px;
$breakpoint: 768px;
$iphone6-width: 375px;
$iphone5-width: 320px;


@mixin breakpoint {
  @media (min-width: #{$breakpoint}) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: #{$desktop-width}) {
    @content;
  }
}

@mixin tinyscreen {
  @media (max-width: #{$iphone5-width}) {
    @content;
  }
}

@mixin mobile {
  @media (min-width: #{$iphone5-width}) and (max-width: #{$iphone6-width}) {
    @content;
  }
}

@mixin mobile-side {
  @media (min-height: #{$iphone5-width}) and (max-height: #{$iphone6-width}) {
    @content;
  }
}

@mixin retina {
  @media
  only screen and (-webkit-min-device-pixel-ratio: 2),
  only screen and (min--moz-device-pixel-ratio: 2),
  only screen and (-o-min-device-pixel-ratio: 2/1),
  only screen and (min-device-pixel-ratio: 2),
  only screen and (min-resolution: 192dpi),
  only screen and (min-resolution: 2dppx) {
    @content;
  }
}

@mixin print {
  @media print {
    @content;
  }
}

@mixin link-underline {
  &:hover {
    &:before {
      height: 2px;
      opacity: .7;
      transform: scaleX(1);
      bottom: 4px;
    }
  }

  &:before {
    content: "";
    position: absolute;
    height: 1px;
    width: 100%;
    transform: scaleX(.75);
    bottom: 2px;
    left: 0;
    background-color: $dj-black;
    opacity: .1;
    transition: all 150ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
}

@mixin height-after-header($offset: "0rem") {
  height: calc(100vh - (#{$header-height} + #{$offset});
}

$dj-header-font: "Ostrich Sans";
$dj-header-font-weight: 400;
$dj-body-font: "Ek-Mukta";
$dj-body-font-weight: 100;

// from http://www.thedayscolor.com/wallpapers/052614_pgso.jpg
$dj-white: #FAF;
$dj-light-gray: #C2C3C5;
$dj-medium-gray: #686868;
$dj-black: #161616;
$dj-blue: #2B418C;

$dj-white: hsl(0, 0%, 97%);
$header-height: 3rem;
$header-height-desktop: 3rem;
$footer-height: 3rem;

body {
  font-family: $dj-body-font;
  font-weight: $dj-body-font-weight;
}

h1, h2, h3, h4, h5, h6 {
  font-family: $dj-header-font;
  font-weight: $dj-header-font-weight;
}

// LAYOUT
//$susy: (
//  columns: 12,  // The number of columns in your grid
//  gutters: 1/4, // The size of a gutter in relation to a single column
//);

* {
  box-sizing: border-box;
}

a {
  
}

section a, article a, footer a {
  position: relative;
  text-decoration: none;
  white-space: nowrap;
  word-break: keep-all;

  @include link-underline();
}

body {
  margin: 0px;
  background-color: $dj-white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  -webkit-font-smoothing: subpixel-antialiased!important; // for safari's weird jumpy text during any transform

  > section, article {
    flex: 0 1 auto;
    min-height: calc(100vh - #{$header-height + $footer-height});
    width: 100%;

    @include breakpoint {
      min-height: calc(100vh - #{$header-height-desktop + $footer-height});
    }
  }
}

footer {
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: relative;
  width: 100%;
  height: $footer-height;
  border-top: solid 1px $dj-light-gray;
  background-color: inherit;
  overflow: hidden;
  text-align: center;

  a, span {
    flex: 0 1 auto;
  }
}

header {
  flex: 0 0 auto;
  width: 100%;
  position: relative;
  z-index: 1;
  height: $header-height;
  font-family: $dj-header-font;
  font-weight: 400;
  font-size: 3rem;
  line-height: 1;
  border-bottom: solid 1px $dj-light-gray;

  @include mobile-side() {
    height: 2rem;
  }
}

.home header {
  border-bottom: none;

  #logo {
    visibility: hidden;
  }
}

html {
  min-height: 100vh;
}

nav {
  position: relative;
  text-align: center;
  // display: flex;
  // justify-content: space-around;
  // align-items: center;
  height: 100%;

  a {
    text-decoration: none;
    color: $dj-black;
  }
}

object {
  border: none;
}

// STLYES

img {
  max-width: 100%;
}

p {
  font-size: 1.15rem;
}

blockquote + figcaption cite {
  display: inline;
}

// .bq { @include typl8-blockquote("—"); }

.blog-box {
  display: flex;
  flex-direction: column;
  justify-content: center;


  ul {
    min-height: 225px;
    margin: 0 auto;
    padding: 0;
    font-size: 1.1em;

    li {
      &:last-child {
        list-style-type: none;
      }
    }
  }
}

.center {
  text-align: center;
}

.hidden {
  display: none!important;

  &:before, &:after {
    display: none!important;
  }
}

.lead {
  position: relative;
  min-height: 200px;
  height: calc(100vh - #{$header-height});
  text-align: center;
  font-size: 1.1em;

  h1 {
    font-size: 4em;
    margin: 0;
    padding: .75rem 0 .01rem 0;

    @include mobile-side() {
      padding: 0 0 .01rem 0;
    }
  }

  object {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;

    @include tinyscreen() {
      // pointer-events: all;
      top: 2.2rem;
    }

    @include mobile-side() {
      // top: 1.0rem;
    }
  }

  @media (min-height: 800px) and (max-height: 1399px) {
    font-size: 1.25em;

    h1 {
      padding: 3rem 0 0.03rem 0;
    }
  }

}

.nav-links {
  display: flex;
  z-index: 10;
  padding: .5em 0;
  flex: 2 1 auto;
  flex-direction: column;
  justify-content: space-around;
  $font-size:  map-get($typl8-scale,"typl8-tera");
  font-size: $font-size + px;
  height: 0px;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s cubic-bezier(0.7, 0.01, 0.3, 1);

  @include tinyscreen() {
    padding: .2em 0;
    font-size: 1.75em;
  }

  @include mobile() {
    padding: .5em 0 1em 0;
  }

  @include mobile-side() {
    // padding: .2em 0;
    font-size: 1.75em;
  }

    > a {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      height: 100%;
      padding: .05em 0;
      text-decoration: none;
      color: $dj-blue;

      &:hover {
        background-color: $dj-light-gray;
      }
      

      > span {
        opacity: 0;
        transition: all 0.2s cubic-bezier(0.755, 0.05, 0.855, 0.06);
        flex: 0 1 auto;
      }
    }
}

.open-menu-label {
  position: absolute;
  left: 0;
  color: transparent;
  height: $header-height;
  width: 1em;
  cursor: pointer;
  // -webkit-font-smoothing: subpixel-antialiased;
  // -webkit-font-smoothing: none;

  &:before, &:after {
    content: "";
    display: block;
    -webkit-font-smoothing: none;
    color: $dj-black;
    position: absolute;
    // left: 10px;
    left: calc(.25em);
    top: 20px;
    background-color: $dj-black;
    height: 3px;
    width: 25px;;
    transition: all 0.5s cubic-bezier(0.7, 0.01, 0.3, 1);
    tranform: rotate(0deg);
  }

  &:after {
    top: 28px;
    // height: .055em;
  }
}

.post {
  padding: 1em .5em;
}

.projects-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 225px;
  text-align: center;

  $gif-width: 360px;

  @include breakpoint {
    flex-direction: row;
    width: 3 * $gif-width;
    max-width: 100%;
    margin: 0px auto;
  }

  div {
    @include breakpoint {
      width: 32vw;
      max-width: $gif-width;
      margin: 0;
    }
  }

  img {
    display: block;
    margin: 0 auto;
    position: relative;
  }

}

.section-header {
  margin: 2rem 0 2rem;
  text-align: center;
}

.blog-box, .projects-box {
  margin-bottom: 4em;
}

.what-i-do {
  position: relative;
  font-weight: 400;
  color: $dj-blue;

  &:before {
    display: none;
  } 

  &:after {
    content: "»";
    color: inherit;
    position: absolute;
    font-size: 1.25em;
    bottom: -.9em;
    left: 50%;
    transform: rotate(90deg);
    transition: all 0.1s cubic-bezier(0.755, 0.05, 0.855, 0.06);
  }

  &:hover {
    &:after {
      bottom: -1.15em;
    }
  }
}

#logo {
  display: inline-block;
  font-family: "Ostrich Sans Medium";
}

#logo-container {
  transition: all 0.15s cubic-bezier(0.755, 0.05, 0.855, 0.06);
}

#open-menu {
  position: absolute;
  left: -100px;
  top: -100px;
  visibility: hidden;
  height: 1px;
  width: 1px;

  &:checked + .open-menu-label {

    $angle: 45deg;
    $perspective: 1px;
    $top-val: 24px;

    &:before {
      top: $top-val;
      transform: rotate($angle);
    }

    &:after {
      top: $top-val;
      transform: rotate(-$angle);
    }
  }

  &:checked ~ #logo-container {
    background: $dj-white;
    transition: all 0.3s cubic-bezier(0.755, 0.05, 0.855, 0.06);
  }

  &:checked ~ .nav-links {
    visibility: visible;
    opacity: 1;
    background-color: $dj-white;
    @include height-after-header();

    @include mobile-side() {
      padding: 0;
      @include height-after-header($offset: "0rem");
    }

    > a {
      > span {
        opacity: 1;
        transition: all 0.4s cubic-bezier(0.755, 0.05, 0.855, 0.06);
      }
    }
  }
}

#projects-nav {
  > span {
    &:hover {
      &:before {
        height: 2px;
        opacity: 1;
        transform: scaleX(1);
      }
    }

    &:before {
      transform: scaleX(.875);
    }
  }
}